<template>
  <div class="params">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        show-icon
        effect="dark"
      >
      </el-alert>
      <span>选择商品分类：</span>
      <el-cascader
        v-model="value"
        :options="categoryAllList"
        :props="props"
      ></el-cascader>
      <!-- 标签页 -->
      <el-tabs type="border-card" @tab-click="tabClick">
        <el-tab-pane label="动态参数">
          <ParamsTable :attr_sel="attr_sel" :classifyId="classifyId" />
        </el-tab-pane>
        <el-tab-pane label="静态属性">
          <ParamsTable :attr_sel="attr_sel" :classifyId="classifyId" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import ParamsTable from "../components/ParamsTable.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: { ParamsTable },
  data() {
    return {
      props: {
        label: "cat_name",
        value: "cat_id",
        expandTrigger: "hover",
      },
      value: [],
      classifyId: "",
      attr_sel: "only",
    };
  },
  created() {
    this.getGoodsCategory({ type: 3 });
  },
  computed: {
    ...mapState({
      categoryAllList: (state) => state.goodsCategory.categoryAllList,
      attributes: (state) => state.params.attributes,
    }),
  },
  watch: {
    value(newValue) {
      this.classifyId = newValue[newValue.length - 1];
      // console.log(this.classifyId);
      this.getAttributes({ id: this.classifyId, sel: "many" });
    },
  },
  methods: {
    tabClick(rs) {
      if (this.classifyId) {
        if (rs.index === "0") {
          this.attr_sel = "many";
          this.getAttributes({ id: this.classifyId, sel: "many" });
        } else {
          this.attr_sel = "only";
          this.getAttributes({ id: this.classifyId, sel: "only" });
        }
      }
    },
    ...mapActions(["getGoodsCategory", "getAttributes"]),
  },
};
</script>

<style lang="less">
.el-breadcrumb {
  font-size: 12px;
  margin-bottom: 20px;
}
.el-cascader-menu {
  height: 200px;
}
.params {
  .el-cascader {
    margin: 15px 0;
  }
}
</style>
