<template>
  <div class="params-dialog">
    <el-dialog
      :title="ruleForm.attr_id ? '修改动态参数' : '添加动态参数'"
      :visible.sync="dialogVisible"
      width="50%"
      @close="$emit('update:dialogVisible', false)"
    >
      <el-form :model="ruleForm" ref="form" label-width="100px" :rules="rules">
        <el-form-item label="动态参数" prop="attr_name">
          <el-input v-model="ruleForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$emit('update:dialogVisible', false)"
          >取 消</el-button
        >
        <el-button type="primary" @click="sure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["dialogVisible", "ruleForm", "classifyId", "attr_sel"],
  data() {
    return {
      rules: {
        attr_name: {
          required: true,
          message: "请输入参数名称",
          trigger: "blur",
        },
      },
    };
  },
  methods: {
    sure() {
      this.$refs.form.validate(async (valida) => {
        if (valida) {
          if (this.ruleForm.attr_id) {
            await this.saveAttributes({
              cateId: this.classifyId,
              attr_id: this.ruleForm.attr_id,
              attr_name: this.ruleForm.attr_name,
              attr_sel: this.attr_sel,
              attr_vals: this.ruleForm.attr_vals,
            });
            this.$message.success("修改成功");
          } else {
            // 添加
            this.addAttributes({
              catId: this.classifyId,
              data: {
                attr_name: this.ruleForm.attr_name,
                attr_sel: this.attr_sel,
              },
            }).then(
              (res) => {
                this.$message.success("添加成功");
              },
              (error) => {
                this.$message.error(error);
              }
            );
          }
          this.$emit("update:dialogVisible", false);
        }
      });
    },
    ...mapActions(["addAttributes", "saveAttributes"]),
  },
};
</script>

<style></style>
