<template>
  <div class="paramsTable">
    <el-button
      @click="add"
      type="primary"
      size="mini"
      :disabled="classifyId == ''"
      >添加参数</el-button
    >
    <el-table :data="attributes" border style="width: 100%">
      <el-table-column type="expand">
        <template v-slot="scope">
          <el-tag
            v-for="(item, i) in filterArr(scope.row.attr_vals)"
            :key="i"
            closable
            @close="handleClose(scope.row, i)"
            >{{ item }}</el-tag
          >
          <!-- 输入的文本框 -->
          <el-input
            class="input-new-tag"
            v-if="visible"
            v-model="inputValue"
            ref="input"
            size="small"
            @keyup.enter.native="handleInputConfirm(scope.row)"
          >
          </el-input>
          <!-- 添加按钮 -->
          <el-button
            v-else
            class="button-new-tag"
            size="small"
            @click="showInput()"
            >+ New Tag</el-button
          >
        </template>
      </el-table-column>
      <el-table-column type="index" label="#"> </el-table-column>
      <el-table-column prop="attr_name" label="参数名称" width="600">
      </el-table-column>
      <el-table-column label="操作">
        <template v-slot="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="edit(scope.row)"
            >修改</el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加修改对话框 -->
    <ParamsDialog
      :ruleForm="ruleForm"
      :dialogVisible.sync="dialogVisible"
      :classifyId="classifyId"
      :attr_sel="attr_sel"
    />
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import ParamsDialog from "./ParamsDialog.vue";
export default {
  components: { ParamsDialog },
  props: ["attr_sel", "classifyId"],
  data() {
    return {
      visible: false,
      inputValue: "",
      dialogVisible: false,
      ruleForm: {},
    };
  },
  computed: {
    ...mapState({
      attributes: (state) => state.params.attributes,
    }),
  },
  methods: {
    filterArr(value) {
      if (value) {
        return value.split(" ");
      }
    },
    handleInputConfirm(row) {
      this.visible = false;
      if (this.inputValue.trim()) {
        // console.log(this.inputValue);
        row.attr_vals = row.attr_vals.length
          ? row.attr_vals + " " + this.inputValue
          : this.inputValue;
        this.saveAttributes({
          cateId: this.classifyId,
          attr_id: row.attr_id,
          attr_name: row.attr_name,
          attr_sel: this.attr_sel,
          attr_vals: row.attr_vals,
        });
      }
    },
    showInput(row) {
      this.visible = true;
      this.inputValue = "";
      this.$nextTick(function () {
        this.$refs.input.focus();
      });
    },
    handleClose(row, index) {
      const arr = this.filterArr(row.attr_vals);
      arr.splice(index, 1);
      row.attr_vals = arr.join(" ");
      this.saveAttributes({
        cateId: this.classifyId,
        attr_id: row.attr_id,
        attr_name: row.attr_name,
        attr_sel: this.attr_sel,
        attr_vals: arr.join(","),
      });
    },
    // 修改
    edit(row) {
      this.ruleForm = { ...row };
      this.dialogVisible = true;
    },
    // 添加
    add() {
      this.ruleForm = {};
      this.dialogVisible = true;
    },
    ...mapActions(["saveAttributes", "delAttributes"]),
  },
};
</script>

<style lang="less">
.paramsTable {
  .el-tag {
    margin: 0 8px;
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .el-button {
    margin-bottom: 15px;
  }
}
</style>
